<template>
  <div class="post-motion">
    <h2>Proposal:</h2>
    <div class="content">
      <Tabs @click="onClickTab" animated>
        <Tab title="Major proposal">
          <div style="margin: 5px 0 10px 18px">
            <h3>Voting on major issues (MORE THAN HALF NUMBER WINS).</h3>
            <p style="margin: 5px 0">
              1. Formulate and amend the Basic Law, formulate rules for
              proposal,decision-making and supervision, etc.
            </p>
            <p>2. Election of global representatives.</p>
            <p style="margin: 5px 0">
              3. Formulation of specific content rules./p>
            </p>
            <p>4. The vote option will be included.</p>
          </div>
          <Field
            v-model="newName"
            rows="1"
            autosize
            label="Proposal Name"
            type="textarea"
            placeholder="Please input the Proposal Name"
          >
          </Field>
          <Field
            v-model="newErcAdd"
            rows="1"
            autosize
            label="ERC20 Address"
            type="textarea"
            placeholder="Please input the ERC20"
          >
          </Field>

          <Field
            v-model="newInfo"
            rows="1"
            autosize
            label="Proposal Info"
            type="textarea"
            placeholder="Please input the proposal introduction, related links, and public welfare project contact person"
          >
          </Field>

          <!-- Proposal Option -->
          <div
            v-for="(item, index) in form.options"
            :key="index"
            class="optionBox"
          >
            <Field
              v-model="item.option"
              rows="1"
              autosize
              label="Option"
              type="textarea"
              placeholder="Please input the Proposal Option"
            >
            </Field>

            <span
              v-if="index >= 0"
              class="button-rounded button-small size"
              @click="addSearchOptions"
              :style="{ marginRight: index == 0 ? '3px' : '' }"
            >
              <i
                class="fa fa-plus-square size"
                style="color: #a5de37; font-size: 24px"
              ></i>
            </span>

            <span
              v-if="index >= 1"
              class="button-rounded button-small size"
              @click="delSearchOptions(index)"
              style="margin-right: 3px"
            >
              <i
                class="fa fa-minus-square size"
                style="color: #ff4351; font-size: 24px"
              ></i>
            </span>
          </div>

          <!-- 有效时间 -->
          <Field
            v-model="time"
            type="number"
            label="Valid Time"
            placeholder="Please input the Valid Time"
          >
          </Field>
          <!-- 操作 -->
          <div class="btnbox">
            <Button type="default" @click="resetOptions"> Cancel </Button>
            <Button type="info" :disabled="disabled" @click="handlerPostMotion">
              Confirm
            </Button>
          </div>
        </Tab>

        <Tab title="General Proposal">
          <div style="margin: 5px 0 10px 18px">
            <h3>
              Voting on general proposal (the winner with the first number of
              votes).
            </h3>
            <p style="margin: 5px 0">
              1. Charity to which you donate each month.
            </p>
            <p>2. The Use of Treasury Fund (Celebrity Award).</p>
            <p style="margin: 5px 0">
              3. Election of regional public opinion representatives (Regional
              representative: responsible for collecting and verifying
              humanitarian aid projects around the world) .
            </p>
            <p>4. The vote option will be included.</p>
          </div>
          <Field
            v-model="newName"
            rows="1"
            autosize
            label="Proposal Name"
            type="textarea"
            placeholder="Please input the Proposal Name"
          >
          </Field>
          <Field
            v-model="newErcAdd"
            rows="1"
            autosize
            label="ERC20 Address"
            type="textarea"
            placeholder="Please input the ERC20"
          >
          </Field>

          <Field
            v-model="newInfo"
            rows="1"
            autosize
            label="Proposal Info"
            type="textarea"
            placeholder="Please input the proposal introduction, related links, and public welfare project contact person"
          >
          </Field>

          <!-- Proposal Option -->
          <div
            v-for="(item, index) in form.options"
            :key="index"
            class="optionBox"
          >
            <Field
              v-model="item.option"
              rows="1"
              autosize
              label="Option"
              type="textarea"
              placeholder="Please input the Proposal Option"
            >
            </Field>

            <span
              v-if="index >= 0"
              class="button-rounded button-small size"
              @click="addSearchOptions"
            >
              <i
                class="fa fa-plus-square size"
                style="color: #a5de37; font-size: 24px"
              ></i>
            </span>

            <span
              v-if="index >= 1"
              class="button-rounded button-small size"
              @click="delSearchOptions(index)"
            >
              <i
                class="fa fa-minus-square size"
                style="color: #ff4351; font-size: 24px"
              ></i>
            </span>
          </div>

          <!-- 有效时间 -->
          <Field
            v-model="time"
            type="number"
            label="Valid Time"
            placeholder="Please input the Valid Time"
          >
          </Field>
          <!-- 操作 -->
          <div class="btnbox">
            <Button type="default" @click="resetOptions"> Cancel </Button>
            <Button type="info" :disabled="disabled" @click="handlerPostMotion">
              Confirm
            </Button>
          </div>
        </Tab>
      </Tabs>
    </div>
  </div>
</template>

<script>
import { Field, Tab, Tabs } from "vant";
import { RadioGroup, Radio } from "vant";
import { Button } from "vant";
import { mapState, mapMutations } from "vuex";
import { formartadd, setLocalStorage, getLocalStorage } from "@/utils/utils";
import $web3js from "../../../lib/contract/web3";
import numberUtils from "@/utils/numberUtils";
const Web3 = require("web3");
const iecvote = require("@/lib/contract/contractAbi/iecvote.json");
export default {
  data() {
    return {
      disabled: true,
      // 修改议案提交名称拼接
      newName: "",
      newErcAdd: "",
      newInfo: "",

      form: {
        name: "",
        options: [
          {
            option: "",
          },
        ],
      },
      // 有效时间
      time: "",
      // 是否重要
      radio: 1,

      // 提案列表数据
      motionListData: [],

      vote: this.$contractAddress.vote,
      iecConst: "",
    };
  },

  components: {
    Field,
    RadioGroup,
    Radio,
    Button,
    Tab,
    Tabs,
  },
  computed: {
    ...mapState("login", ["walletaccount"]),
  },
  watch: {
    walletaccount: {
      handler(val) {
        if (val != "") {
          this.disabled = false;
        }
      },
    },
    deep: true,
    immediate: true,
  },

  activated() {
    this.disabled = getLocalStorage("walletaccount") ? false : true;
  },

  methods: {
    // tab click
    onClickTab(name, title) {
      if (title == "Major proposal") {
        this.radio = 1;
      } else {
        this.radio = 2;
      }
      this.resetOptions();
    },

    // 发布
    handlerPostMotion() {
      if (this.time > 30) {
        this.$toast({
          message: "The valid time of a proposal can not exceed 30 days",
        });
        return;
      } else if (this.time < 1) {
        this.$toast({
          message: "The valid time of a proposal can not be less than 1 day",
        });
        return;
      }
      this.form.name = this.newName + "|" + this.newErcAdd + "|" + this.newInfo;
      //  console.log("o o", this.form.name);
      this.writeMotion();
    },
    // 写提案
    async writeMotion() {
      // try catch
      this.connectMetaMask();
      if (getLocalStorage("walletaccount")) {
        this.web3 = this.$web3js.getWeb3();
      } else {
        // 未连接钱包时， 默认连接到 bnb 测试
        this.web3 = new Web3(
          "https://mainnet.infura.io/v3/a1fb8b522ffc46d38c505a38cb073e73"
        );
      }

      this.myaddress = getLocalStorage("walletaccount");
      this.iecConst = new this.web3.eth.Contract(iecvote.abi, this.vote, {
        from: this.myaddress,
      });

      // 拼接 Proposal Name
      // this.form.name = this.newName + "|" + this.newErcAdd + "|" + this.newInfo;
      // let _name = $web3js.stringToBytes32(this.form.name);
      let _name = (this.form.name =
        this.newName + "|" + this.newErcAdd + "|" + this.newInfo);
      let _days = this.time;
      let _isMajorProposal = this.radio == 1 ? true : false;
      let _AOptions,
        _BOptions,
        _COptions,
        _DOptions,
        _FOptions = "";
      if (this.form.options.length == 1) {
        _AOptions = this.form.options[0].option;
        _BOptions = "NO";
        _COptions = "NO";
        _DOptions = "NO";
        _FOptions = "Reject all options";
      } else if (this.form.options.length == 2) {
        _AOptions = this.form.options[0].option;
        _BOptions = this.form.options[1].option;
        _COptions = "NO";
        _DOptions = "NO";
        _FOptions = "Reject all options";
      } else if (this.form.options.length == 3) {
        _AOptions = this.form.options[0].option;
        _BOptions = this.form.options[1].option;
        _COptions = this.form.options[2].option;
        _DOptions = "NO";
        _FOptions = "Reject all options";
      } else if (this.form.options.length == 4) {
        _AOptions = this.form.options[0].option;
        _BOptions = this.form.options[1].option;
        _COptions = this.form.options[2].option;
        _DOptions = this.form.options[3].option;
        _FOptions = "Reject all options";
      }
      let vm = this;
      let messageNo;

      vm.iecConst.methods
        .proposal(
          _name,
          _AOptions,
          _BOptions,
          _COptions,
          _DOptions,
          _FOptions,
          _isMajorProposal,
          _days
        )
        .send()
        .on("transactionHash", function (hash) {
          vm.hash = hash;
          messageNo = vm.$toast({
            duration: 0,
            message: "In transaction, please wait patiently",
          });
        })
        .on("receipt", function (receipt) {
          // console.log("交易回执", receipt);
          if (receipt.transactionHash == vm.hash) {
            messageNo.close();
            vm.$toast({
              message: "提案成功",
            });
            vm.motionListData = [];
            // 重置选项
            vm.resetOptions();
            vm.getvoteList();
          }
        })
        .on("error", function (error, receipt) {
          // console.log("交易失败回执", error, receipt);
          messageNo.close();
          vm.$toast({
            message: "Proposal failed, please try again later",
          });
        });
    },

    // Add an option
    addSearchOptions() {
      if (this.form.options.length < 4) {
        this.form.options.push({
          option: "",
        });
      } else {
        this.$toast({
          message: "No more than four options of a proposal",
          type: "warning",
        });
      }
    },

    // Delete an option
    delSearchOptions(index) {
      this.form.options.splice(index, 1);
    },

    // 重置参数
    resetOptions() {
      this.newName = "";
      this.newErcAdd = "";
      this.newInfo = "";
      let formRest = {
        name: "",
        options: [
          {
            option: "",
          },
        ],
      };
      this.form = JSON.parse(JSON.stringify(formRest));
    },
    connectMetaMask() {
      $web3js.connectMetaMask().then((res) => {
        if (!getLocalStorage("walletaccount")) {
          // this.loadingData();
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.post-motion {
  padding-bottom: 20px;
  > h2 {
    font-size: 16px;
    padding: 15px;
  }
  .content {
    padding: 0 15px;
    h3 {
      font-size: 17px;
      font-weight: normal;
    }
    p {
      font-size: 16px;
    }
    .optionBox {
      display: flex;
      align-items: center;
      width: 100%;
      .size {
        display: flex;
        align-items: center;
        width: 32px;
        height: 32px;
      }
    }
    .btnbox {
      display: flex;
      margin-top: 45px;
      justify-content: center;
      > button:nth-child(1) {
        margin-right: 30px;
      }
    }
  }
}

/deep/ .van-cell {
  margin: 15px 0;
}
/deep/ .van-field__label {
  width: auto;
}

/deep/ .van-tabs__nav {
  background: #aedafd;
}
/deep/ .van-tabs__line {
  background-color: #209cff;
}
/deep/ .van-tab__text {
  font-size: 15px;
  font-weight: 600;
}
</style>
